const INIITIAL_STATE ={
    users: [
      {
        name:"Ghous",
    email:"ghous@gmail.com"
   }
  ]
}
export default (state = INIITIAL_STATE,action)=>{
  switch(action.type){
    case "SETDATA":
  
  return ({
    ...state,
    users: [...state.users,action.users]
  })

}
return  state;
}
